<template>
  <div :id="domId"></div>
</template>

<script type="text/ecmascript-6">
  export default {
    mounted () {
      this.domId = this.getUID(this.domId);
      let config = {};

      // 
      Object.assign(config, this.defaultConfig, this.config);
      config.toolbar = this.toolbar.length > 0 ? this.toolbar : this.defaultToolbar;

      //  DOM  Id  DOM 
      this.$nextTick(() => {
        this.init(this.domId, config);
      });
    },
    props: {
      content: {
        type: String,
        default: ''
      },
      config: {
        type: Object,
        default: () => {
          return {};
        }
      },
      toolbar: {
        type: Array,
        default: () => {
          return [];
        }
      }
    },
    data () {
      return {
        um: null,
        domId: 'J_UMeditor',
        defaultToolbar: [ // umeditor.config.js
          'source',
          '|',
          ' undo redo',
          '|',
          ' bold italic underline strikethrough horizontal',
          '|',
          ' forecolor backcolor',
          '|',
          'insertorderedlist insertunorderedlist',
          '|',
          'paragraph fontfamily fontsize' ,
          '|',
          'justifyleft justifycenter justifyright justifyjustify',
          '|',
          'link unlink',
          '|',
          ' removeformat',
          '|',
          'image'
        ],
        defaultConfig: { // UMeditor  umeditor.config.js
          serverUrl: '', // 
          zIndex: 1050, //  900 modal-backdrop  z-index = 1040
          initialFrameWidth: '100%', // 
          initialFrameHeight: 200, //  px
          autoClearinitialContent: true // 
        }
      }
    },
    methods: {
      getUID (prefix) {
        do {
          prefix += ~~(Math.random() * 1000000);
        }
        while (document.getElementById(prefix));

        return prefix;
      },
      init (domId, config) {
        this.um = UM.getEditor(domId, config);
        this.bind();
      },
      bind () {
        //  content
        this.um.ready(() => {
          this.um.setContent(this.content);
        });

        // emit  change 
        this.um.addListener('contentChange', () => {
          this.$emit('change', this.getContent());
        });
      },
      reset () {
        this.um.reset();
        this.um.setContent('');
      },
      destroy () {
        this.um.destroy();
      },
      setContent (content) {
        this.um.setContent(content);
      },
      getContent () {
        return this.um.getContent();
      }
    },
    watch: {
      content (val) {
        this.um.setContent(val);
      }
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss">

</style>



